.blog {
  padding: 10rem 0;
  position: relative; }
  .blog-title {
    display: flex;
    justify-content: center;
    margin-bottom: 5rem; }
    .blog-title > h5 {
      font-size: 4.8rem;
      line-height: 4.8rem;
      color: #000; }
  .blog-inner {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .blog-card {
    border: 0.1rem solid #F0F8F6;
    max-width: 38.7rem;
    height: 53.1rem;
    transition: all .3s linear;
    background: #fff; }
    .blog-card__img {
      height: 21.8rem;
      width: 100%; }
      .blog-card__img > img {
        height: 100%;
        width: 100%; }
    .blog-card__title {
      margin-bottom: 4.2rem; }
      .blog-card__title > * {
        font-size: 2.4rem;
        line-height: 2.4rem;
        letter-spacing: 0.05em; }
      .blog-card__title--mb {
        margin-bottom: 2.4rem; }
    .blog-card__text {
      margin-bottom: 3.7rem; }
      .blog-card__text > p {
        color: #696969;
        font-size: 1.4rem;
        line-height: 150%; }
    .blog-card__line {
      width: 6.0rem;
      height: .7rem;
      margin-bottom: 3rem; }
      .blog-card__line > img {
        display: block;
        object-fit: contain;
        width: 100%;
        height: 100%; }
    .blog-card__link {
      display: flex;
      width: 100%;
      justify-content: flex-end; }
      .blog-card__link > a {
        border: .1rem solid transparent;
        border-radius: 5rem;
        padding: .8rem 2rem;
        background-color: #01A3A4;
        font-size: 1.2rem;
        line-height: 1.4rem;
        color: #fff;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        transition: all .3s linear; }
        .blog-card__link > a:hover {
          border: 0.1rem solid #01A3A4;
          background-color: #fff;
          color: #01A3A4; }
    .blog-card__wrap {
      max-width: 100%;
      padding: 4.2rem 4rem 4rem 4rem; }
  .blog-bg {
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    z-index: 0; }
    .blog-bg > svg {
