.posts {
  padding: 10rem 0;
  background-color: #F0F8F6; }
  .posts-top__text {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 5rem; }
    .posts-top__text-subtitle {
      color: #01A3A4;
      font-size: 1.4rem;
      line-height: 1.6rem;
      letter-spacing: 0.25em;
      margin-bottom: 2rem; }
    .posts-top__text-title > h3 {
      color: #212121;
      font-size: 4.8rem;
      line-height: 100%; }
  .posts-inner {
    display: grid;
    grid-gap: .9rem;
    justify-content: center;
    grid-template-areas: "post-block-1 post-block-2 post-block-lg post-block-7" "post-block-3 post-block-4 post-block-lg post-block-8";
    grid-template-columns: repeat(2, minmax(1fr, 19rem)) 2fr minmax(1fr, 19rem);
    grid-template-rows: repeat(2, 1fr); }
  .posts-block {
    width: 23.9rem;
    height: 23.9rem; }
    .posts-block > img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .posts-block__left {
      position: relative;
      grid-area: post-block-1;
      width: 100%;
      height: 23.9rem; }
      .posts-block__left > picture > img {
        display: block;
        max-width: 23.9rem;
        max-height: 23.9rem; }
    .posts-block__right {
      position: relative;
      grid-area: post-block-7;
      width: 100%;
      height: 23.9rem; }
      .posts-block__right > picture > img {
        display: block;
        max-width: 23.9rem;
        max-height: 23.9rem; }
    .posts-block__large {
      grid-area: post-block-lg;
      max-width: 47.7rem;
      height: 48.7rem; }
      .posts-block__large > img {
        width: 100%;
        height: 100%; }
    .posts-block-eight {
      grid-area: post-block-8;
      width: 100%;
      height: 100%; }
      .posts-block-eight > img {
        max-width: 23.3rem;
        height: 23.3rem; }
    .posts-block-two {
      grid-area: post-block-2; }
    .posts-block-three {
      grid-area: post-block-3; }
    .posts-block__circle-left {
      width: 6.6rem;
      height: 6.6rem;
      position: absolute;
      top: -2.3rem;
      left: -1.7rem; }
      .posts-block__circle-left::before {
        position: absolute;
        content: "Instagram";
        text-transform: uppercase;
        font-size: 12.5rem;
        line-height: 100%;
        color: #ffffff;
        opacity: 0.5;
        transform: rotate(90deg);
        right: -15.8rem;
        bottom: -30rem; }
    .posts-block__circle-right {
      width: 4.9rem;
      height: 4.9rem;
      display: block;
      position: absolute;
      top: -2.5rem;
      right: -2rem;
      z-index: 3; }
      .posts-block__circle-right > img {
        object-fit: cover;
        width: 4.9rem;
        height: 4.9rem; }
    .posts-block__img-left {
      width: 100%;
      height: 100%; }
      .posts-block__img-left > img {
        width: 100%;
        height: 100%;
        object-fit: cover; }
    .posts-block__img-right {
